
import api from './server'
import navigate from './utils/navigate'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components:{
Cell
},
data () {
return {
msg: '',
appShow:false
}
},
beforeCreate(){
var _this = this;
var bower = navigate();

let paramObj = {};
if(location.search){
const paramStr = location.search.split('?')[1];
const temp = paramStr.split('&');
temp.map(function (item) {
paramObj[item.split('=')[0]] = item.split('=')[1];
});
};
if(api.MODEL == 'dev'){
if(localStorage.openid){
_this.$store.state.openid = localStorage.openid;
}
if(localStorage.token){
_this.$store.state.token = localStorage.token;
}
if(localStorage.phone){
_this.$store.state.phone = localStorage.phone;
}
}

if(bower == 'wechat'){//
if(_this.$store.state.openid){
setTimeout(function () {
_this.appShow = true;
},0);
return false;
}
if(!paramObj.code){
const href = encodeURIComponent(location.origin + '/' + location.hash);
let appid = '';
if(api.MODEL === 'dev'){
appid = 'wx46a9ea4777569785';
}else if(api.MODEL === 'dist'){
appid = 'wxf7335c0beef7ae9c';
}
location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid='+ appid +'&redirect_uri='+ href +'&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect';
}
localStorage.getOpenIdTimes = localStorage.getOpenIdTimes || 0; 
if(paramObj.code && localStorage.code != paramObj.code){ //openiduserid
localStorage.code = paramObj.code;
api.getOpenId({code:paramObj.code}).then(function (res) {
localStorage.getOpenIdTimes ++;
if(res.code === '000'){
localStorage.removeItem('getOpenIdTimes');
const data = JSON.parse(res.data);
console.log(data);
_this.appShow =true;
if(data.subscribe == '1'){
_this.$store.state.openid = localStorage.openid = data.openId || '';
_this.$store.state.token = localStorage.token = data.token || '';
_this.$store.state.phone = localStorage.phone = data.phone || '';
}else {
_this.$router.replace('/error');
}
}else {
if(localStorage.getOpenIdTimes < 2){
const href = encodeURIComponent(location.origin + '/' + location.hash);
let appid = '';
if(api.MODEL === 'dev'){
appid = 'wx46a9ea4777569785';
}else if(api.MODEL === 'dist'){
appid = 'wxf7335c0beef7ae9c';
}
location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid='+ appid +'&redirect_uri='+ href +'&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect';
}else {
_this.appShow = true;
_this.$router.replace('/error');
localStorage.getOpenIdTimes = 0;
}
}
})
}else {
setTimeout(function () {
_this.appShow = true;
},0)

}
}else if(bower == 'heshenghuo'){//
setTimeout(function () {
_this.appShow = true;
},0);
var s = $("#s").val();
$(function () {
if (hsh.isApp) {
hsh.hshReady(function () {
hsh.getToken(function (obj) {
if(obj != null && obj != ''){
hbxLogin(obj.HARToken, s);
}else {//
var routername = _this.$route.name;
location.href = '#/login/'+encodeURIComponent(routername)
}
});
});
}
function hbxLogin(token,s){
api.hshlogin({token:token}).then(function (res) {
if(res.code === '000'){
var data = JSON.parse(res.data);
_this.$store.state.token = localStorage.token = data.token || '';
_this.$store.state.phone = localStorage.phone = data.cellPhone || '';
}
})
}
})
}else {
setTimeout(function () {
_this.appShow = true;
},0)
}
},
created(){
(function calcHtmlFontSize() {//rem
let width = document.body.clientWidth;
if(width < 320) width = 320;
else if(width > 750) width = 750;
document.getElementsByTagName('html')[0].style.fontSize = width*100/375 + 'px';
window.onresize = function () {
let width = document.body.clientWidth;
if(width < 320) width = 320;
else if(width > 750) width = 750;
document.getElementsByTagName('html')[0].style.fontSize = width*100/375 + 'px';
};
})();
},
mounted(){

}
}



