import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

export default new Router({
  routes:[
    {
      path:'/login/:routername',
      component:function (resolve) {
        require(['./view/login.vue'],resolve)
      },
      name:'login',
      meta:{
        title:''
      }
    },
    {
      path:'/login',
      redirect:'/login/0'
    },
    {
      path:'/register/:phone/:routername',
      component:function (resolve) {
        require(['./view/registe.vue'],resolve)
      },
      name:'register',
      meta:{
        title:''
      }
    },
    {
      path:'/orderlist',
      component:function (resolve) {
        require(['./view/orderlist.vue'],resolve)
      },
      name:'orderlist',
      meta:{
        title:''
      }
    },
    {
      path:'/orderdetail',
      component:function (resolve) {
        require(['./view/orderdetail.vue'],resolve)
      },
      name:'orderdetail',
      meta:{
        title:''
      }
    },
    {
      path:'/freedoclist',
      component:function (resolve) {
        require(['./view/freedoclist.vue'],resolve)
      },
      name:'freedoclist',
      meta:{
        title:''
      }
    },
    {
      path:'/myconsult',
      component:function (resolve) {
        require(['./view/myconsult.vue'],resolve)
      },
      name:'myconsult',
      meta:{
        title:''
      }
    },
    {
      path:'/orderpay',
      component:function (resolve) {
        require(['./view/orderpay.vue'],resolve)
      },
      name:'orderpay',
      meta:{
        title:''
      }
    },
    {
      path:'/clinic/:rid',
      component:function (resolve) {
        require(['./view/clinic.vue'],resolve)
      },
      name:'clinic',
      meta:{
        title:''
      }
    },
    {
      path:'/patientcreate',
      component:function (resolve) {
        require(['./view/patientcreate.vue'],resolve)
      },
      name:'patientcreate',
      meta:{
        title:''
      }
    },
    {
      path:'/paysuccess',
      component:function (resolve) {
        require(['./view/paysuccess.vue'],resolve)
      },
      name:'paysuccess',
      meta:{
        title:''
      }
    },
    {
      path:'/historyconsult',
      component:function (resolve) {
        require(['./view/historyconsult.vue'],resolve)
      },
      name:'historyconsult',
      meta:{
        title:''
      }
    },
    {
      path:'/chat/:servicedetailid/:status',
      component:function (resolve) {
        require(['./view/chat.vue'],resolve)
      },
      name:'chat',
      meta:{
        title:''
      }
    },
    {
      path:'/error',
      component:function (resolve) {
        require(['./view/error.vue'],resolve)
      },
      name:'error',
      meta:{
        title:''
      }
    },
    {
      path:'/mydoctor',
      component:function (resolve) {
        require(['./view/mydoctor.vue'],resolve)
      },
      name:'mydoctor',
      meta:{
        title:''
      }
    },
    {
      path: '/archives/:planid',
      component: function (resolve) {
        require(['./view/archives.vue'],resolve)
      },
      meta: {
        title: ''
      }
    },
    {//
      path:'/archivesstatus/:url',
      component:function (resolve) {
        require(['./view/archives_status.vue'],resolve)
      },
      mata:{
        title:''
      }
    },



  ]
})



