var bowserAgent = function () {
  var ua = navigator.userAgent.toLocaleLowerCase();
  //
  if(ua.indexOf('micromessenger') >= 0 ){
    return 'wechat'
  }
  //
  if(ua.indexOf('heshenghuo') >=0){
    return 'heshenghuo'
  }
  return 'other'
}
module.exports = bowserAgent;



