import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/store'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import regWecaht from './utils/regWechat'
import navigate from './utils/navigate'


Vue.use(AlertPlugin);
Vue.use(ConfirmPlugin);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(WechatPlugin);


router.beforeEach(function (to, from, next) {//
store.state.prefrom = from.name;
document.title = to.meta.title || '';

if(navigate()=='wechat'){
regWecaht(Vue,next);
}else {
next();
}
});

router.afterEach(function (to, from) {//

});


Vue.filter('time',function (value) {

var value = new Date(value);
var vay = value.getFullYear();
var vam = value.getMonth()+1;
var vd = value.getDate();
var vh = value.getHours();
var vs = value.getMinutes();

var nowtime = new Date();
var nowy = nowtime.getFullYear();
var nowm = nowtime.getMonth()+1;
var nowd = nowtime.getDate();
var nowh = nowtime.getHours();
var nows = nowtime.getMinutes();
if(vay<nowy){//
return vay+'-'+(vam>9?vam:'0'+vam)+'-'+(vd>9?vd:'0'+vd)+' '+(vh>9?vh:'0'+vh)+':'+(vs>9?vs:'0'+vs)
}else {
if(vam<nowm){//
return (vam>9?vam:'0'+vam)+'-'+(vd>9?vd:'0'+vd)+' '+(vh>9?vh:'0'+vh)+':'+(vs>9?vs:'0'+vs)
}else {
if(vd == nowd){//
return (vh>9?vh:'0'+vh)+':'+(vs>9?vs:'0'+vs)
}else if(nowd - vd ==1){//
return ' '+(vh>9?vh:'0'+vh)+':'+(vs>9?vs:'0'+vs)
}else {//
return (vam>9?vam:'0'+vam)+'-'+(vd>9?vd:'0'+vd)+' '+(vh>9?vh:'0'+vh)+':'+(vs>9?vs:'0'+vs)
}
}
}

});


new Vue({
router,
store,
el: '#app',
render: h => h(App)
});



